$font-stack: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans", "Helvetica Neue", sans-serif;

//@import "~bootstrap/scss/bootstrap";

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/Roboto-Regular.ttf);
}

.container{
  display:flex;
}
.left-menu{
  width: 250px;
  height: 1200px;

}
.right-menu{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.search-friend{
  width: 400px;
  font-size: 24px;
}
.friends-list{
  display: flex;
  width: 600px;
  flex-wrap: wrap;
}
.info{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 149px;
  height: 200px;
  border: 1px solid;
  border-radius: 20px;
  margin: 20px 20px 20px 20px;
  transition: box-shadow .2s;
  i{
    font-size: 50px;
    margin-top: 69px;
  }
  img{
    border-radius: 19px;
    width: 149px;
    height: 150px;
  }
  &:hover {
    box-shadow: 0px 2px 6px 2px rgba(0, 0, 0, 0.3);
  }
}


.age{
  position: absolute;
  margin: -1px 0 4px 49px;
  width: 52px;
  height: 20px;
  border-radius: 0 0 0 15px;
  text-align: center;
  line-height: 20px;
  color: white;
  font-size: 12px;
  background-color: aqua;
}

.modal{
  display: none;
  justify-content: center;
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

.modal-content{
  width: 400px;
  height: 400px;
  border-radius: 20px;
  background-color: white;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.main-menu{
  display: flex;
  align-items: center;
  flex-direction: column;
  top: 66px;
  position: absolute;
  img{
    border-radius: 68px;
    width: 120px;
    height: 120px;
  }
}
.middle-menu{
  display: flex;
  margin-top: 113px;
}
.bottom-menu{
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin-top: 20px;

}
.info-row__right{
  padding-left: 80px;
}
.choice-gender__box{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.choice-gender__box, .bottom-menu{
  i{
    font-size: 50px;
    color: black;
    &:hover{
      color:red;
    }

  }
}

.choice-age__box{
  display: flex;
  justify-content: space-around;
  align-items: center;
  i{
    font-size: 20px;
    color: black;
    &:hover{
      color:red;
    }
  }
}

input[type="radio"] {
  visibility: hidden;
}

input[type='radio']:hover + label {
  i{
    color:red;
  }
}
input[type='radio']:checked + label {
 i{
   color:#0b69e4;
 }
}
input[type='number']:focus + label  {

  i{color: #0b69e4;}
}

